import ChartDataLabels from 'chartjs-plugin-datalabels';

export const dashHorizonBarOption = {
    // false 일 경우 차트 cavnas 부분의 width 와 height 속성 크기에 맞혀짐
    responsive: true,
    // default value , false일 경우 div의 크기에 맞혀짐
    maintainAspectRatio: false,
    tooltips: {
        // 툴팁 활성여부
        enabled : true,
        // 툴팁에서 어떤 요소를 표현할지 설정
        mode: "nearest",
        //  배경색
        backgroundColor: '#f5f5f5',
        //  제목 폰트 생상
        titleFontColor: '#333',
        //  본문 폰트 색상
        bodyFontColor: '#666',
        //  툴팁 항목의 위와 아래쪽에 공간 추가
        bodySpacing: 4,
        // 툴팁의 왼쪽과 오른쪽에 패딩을 추가
        xPadding: 12,
        // 기본값(true), 툴팁 모드는 마우스 위치가 요소를 교차할때만 적용, false일 경우에는 항상 적용
        intersect: true,
        // 툴팁 위치에 대한 모드 (average : 툴팁에 보여지는 항목들의 평균 위치, nearest : 이벤트 위치에서 가장 가까운 요소의 위치)
        position: "nearest"
    },
    legend: {
        // 범례 출력 여부
        display: false,
    },    
    scales: {
        xAxes: [{
            type : "linear",      // 사용되는 스케일의 유형..??? 
            position : "right",    // 차트 축의 위치를 지정 (top,left,bottom,right)
            offset : true ,      // true이면 양쪽 가장자리에 추가공간이 추가되고 축이 차트 영역에 맞게 조정 / 기본적으로 막대차트에는 true로 설정
            gridLines: {    
                display: false,       // 그리드 선 출력 여부를 설정
            },
            // ticks : 눈금설정
            ticks: {
                display : false,    // 눈금 라벨 출력 여부
                reverse : true,     // 눈금 라벨의 반대 순서
                suggestedMin: 0,
                suggestedMax: 0,    //   최대값을 0
            }
        }],
        yAxes: [{
            position:"right",
            gridLines: {
                display:false,
                drawBorder: false,
            },
            ticks: {
                display : true,
                max : 1,
                min : 0,
                padding: 0,
                fontColor: "#000"
            },
        }]
    }
}

export const multiHorizonBarOption = {
    ...dashHorizonBarOption,
    layout: {
        padding:{
            left: 50,
            right: 50,
        },
    },
    plugins: {
        datalabels: {
            display: true,
            color: '#FFFFFF',
            align: 'center',
            anchor: 'center',
            font: {
                size: 15,
                weight: 'bold',
            },
            formatter(value, context) {                      
                var curValue = context.chart.data.datasets[0].data[context.dataIndex];
                var perValue = curValue / context.chart.data.datasets[0].data[0] * 100;
                if(perValue.toFixed(1) > 25)
                {
                    return context.chart.data.datasets[0].label;
                }
                else
                {
                    return ""
                }               
            },
        },
    }
}

export const singleHorizonBarOption = {
    ...dashHorizonBarOption,
    layout: {
        padding:{
            top: 20,
            left: 50,
            right: 50,
        },
    },
    plugins: {
        datalabels: {
            display: true,
            color: '#FFFFFF',
            align: 'center',
            anchor: 'center',
            font: {
                size: 15,
                weight: 'bold',
            },
            formatter(value, context) {                      
                return context.chart.data.datasets[0].label;            
            },
        },
    }
}


