<template>
    <div class="container dash">
            <div class="inner">
                <div class="input_group top_box">
                </div>
                <div class="graph_box">
                    <h5 class="graph_tit">정검지 현황</h5>
                    <div class="graph_cont">
                        <div class="graph_bd">
                            <div class="graph_item w10p">
                                <span>1급상황</span>
                                <span>2급상황</span>
                            </div>
                            <div class="graph_item w30p">
                                <HorizontalBarChart style="height:100%"
                                    chart-id="bar-chart-h"
                                    :chart-data="normalStopAllChartData"
                                    :extra-options="multiHrznBarOption"
                                ></HorizontalBarChart>
                            </div>
                            <div class="graph_item w30p">
                                <HorizontalBarChart style="height:100%"
                                    chart-id="bar-chart-h"
                                    :chart-data="normalReverseChartData"
                                    :extra-options="multiHrznBarOption"
                                ></HorizontalBarChart>
                            </div>
                            <div class="graph_item w30p">
                                <HorizontalBarChart style="height:100%"
                                    chart-id="bar-chart-h"
                                    :chart-data="normalWalkerCahrtData"
                                    :extra-options="multiHrznBarOption"
                                ></HorizontalBarChart>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="graph_box">
                    <h5 class="graph_tit">오검지 현황</h5>
                    <div class="graph_cont">
                        <div class="graph_bd">
                            <div class="graph_item w10p"></div>
                            <div class="graph_item w30p">
                                <HorizontalBarChart style="height:90%"
                                    chart-id="bar-chart-h"
                                    :chart-data="wrongStopChartData"
                                    :extra-options="singleHrznBarOption"
                                ></HorizontalBarChart>
                            </div>
                            <div class="graph_item w30p">
                                <HorizontalBarChart style="height:90%"
                                    chart-id="bar-chart-h"
                                    :chart-data="wrongReverseChartData"
                                    :extra-options="singleHrznBarOption"
                                ></HorizontalBarChart>
                            </div>
                            <div class="graph_item w30p">
                                <HorizontalBarChart style="height:90%"
                                    chart-id="bar-chart-h"
                                    :chart-data="wrongWalkerChartData"
                                    :extra-options="singleHrznBarOption"
                                ></HorizontalBarChart>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="graph_box third">
                    <h5 class="graph_tit">CCTV 현황</h5>
                    <div class="graph_cont">
                        <div class="graph_hd">
                            <span>미작동</span>
                            <span></span>
                        </div>
                        <div class="graph_bd">
                            <HorizontalBarChart style="height:40%"
                                chart-id="bar-chart-h"
                                :chart-data="cctvChart_one"
                                :extra-options="singleHrznBarOption"
                            ></HorizontalBarChart>
                        </div>
                    </div>
                    <div class="graph_cont">
                        <div class="graph_hd">
                            <span>영역틀어짐</span>
                            <span></span>
                        </div>
                        <div class="graph_bd">
                            <HorizontalBarChart style="height:40%"
                                chart-id="bar-chart-h"
                                :chart-data="cctvChart_two"
                                :extra-options="singleHrznBarOption"
                            ></HorizontalBarChart>
                        </div>
                    </div>
                    <div class="graph_cont">
                        <div class="graph_hd">
                            <span>알림 OFF</span>
                            <span></span>
                        </div>
                        <div class="graph_bd">
                            <HorizontalBarChart style="height:40%"
                                chart-id="bar-chart-h"
                                :chart-data="cctvChart_thr"
                                :extra-options="singleHrznBarOption"
                            ></HorizontalBarChart>
                        </div>
                    </div>
                </div>
            </div>
        </div>
</template>
<script>
import HorizontalBarChart from "@/module/chart/dashboard/dashboardBarChartHorizon"
import { multiHorizonBarOption, singleHorizonBarOption } from '@/module/chart/dashboard/dashboardChartOptions'
import constants from '@/module/chart/config/color'
import chartData from '@/module/chart/dashboard/dashboardChartData'

export default {
    components :{
        HorizontalBarChart,
    },
    created() {
        this.search();
        this.timer = setInterval(()=>this.search(), 300000);
    },
    data(){
        return {
            timer: '',
            normalStopAllChartData: {},
            normalReverseChartData: {},
            normalWalkerCahrtData: {},
            wrongStopChartData: {},
            wrongReverseChartData: {},
            wrongWalkerChartData: {},
            cctvChart_one: {},
            cctvChart_two: {},
            cctvChart_thr: {},
            multiHrznBarOption : multiHorizonBarOption,
            singleHrznBarOption : singleHorizonBarOption,
        }
    },
    mounted() {
        // this.search();
    },
    methods :{
        async search() {

            const result = await $axios.get("/dashboard");

            const resultData = result.data;

            if(result.status == 200) {
                // 정검지 정지차량
                this.normalStopAllChartData = chartData.horizon_bar_chartData(
                    [resultData.normal_stop_all.toString(), resultData.normal_stop_first.toString(), resultData.normal_stop_second.toString()]
                    , "정지차량"
                    , [resultData.normal_stop_all, resultData.normal_stop_first, resultData.normal_stop_second]
                    , constants.chart_yellow
                );
                
                // 정검지 역주행
                this.normalReverseChartData = chartData.horizon_bar_chartData(
                    [resultData.normal_reverse_all.toString(), resultData.normal_reverse_first.toString(), resultData.normal_reverse_second.toString()]
                    , "역주행"
                    , [resultData.normal_reverse_all, resultData.normal_reverse_first, resultData.normal_reverse_second]
                    , constants.chart_red
                );

                // 정검지 보행자
                this.normalWalkerCahrtData = chartData.horizon_bar_chartData(
                    [resultData.normal_walker_all.toString(), resultData.normal_walker_first.toString(), resultData.normal_walker_second.toString()]
                    , "보행자"
                    , [resultData.normal_walker_all, resultData.normal_walker_first, resultData.normal_walker_second]
                    , constants.chart_green
                );

                // 오검지 정지차량
                this.wrongStopChartData = chartData.horizon_bar_chartData(
                    [resultData.wrong_stop_all.toString()]
                    , "정지차량"
                    , [resultData.wrong_stop_all]
                    , constants.chart_yellow
                );
                
                // 오검지 역주행
                this.wrongReverseChartData = chartData.horizon_bar_chartData(
                    [resultData.wrong_reverse_all.toString()]
                    , "역주행"
                    , [resultData.wrong_reverse_all]
                    , constants.chart_red
                );

                // 오검지 보행자
                this.wrongWalkerChartData = chartData.horizon_bar_chartData(
                    [resultData.wrong_walker_all.toString()]
                    , "보행자"
                    , [resultData.wrong_walker_all]
                    , constants.chart_green
                );

                // CCTV 미작동
                this.cctvChart_one = chartData.horizon_bar_chartData(
                    [resultData.cctv_not_working.toString()]
                    , "미작동"
                    , [resultData.cctv_not_working]
                    , constants.chart_blue
                );
                
                // CCTV 영역틀어짐
                this.cctvChart_two = chartData.horizon_bar_chartData(
                    [resultData.cctv_angle_wrong.toString()]
                    , "영역틀어짐"
                    , [resultData.cctv_angle_wrong]
                    , constants.chart_blue
                );

                // CCTV 알림OFF
                this.cctvChart_thr = chartData.horizon_bar_chartData(
                    [resultData.cctv_alarm_on.toString()]
                    , "알림OFF"
                    , [resultData.cctv_alarm_on]
                    , constants.chart_blue
                );

            } else {
                console.log("chart data load fail : ", result)
            }
        },
        destroyRefresh() {
            clearInterval(this.timer);
        }
    },
    beforeDestroy() {
        this.destroyRefresh();
    },
}
</script>


