import {HorizontalBar, mixins} from 'vue-chartjs';
import { dashHorizonBarOption } from '@/module/chart/dashboard/dashboardChartOptions'

export default {
    name: 'bar-chart-h',
    extends: HorizontalBar,
    mixins: [mixins.reactiveProp],
    props: {
        // 차트 옵션 정보
        extraOptions: Object,
    },
    data() {
        return {
            ctx: null,
            options: this.extraOptions,
        };
    },
    methods: {
        updateGradients(chartData) {
            if(!chartData) return;
            const ctx = this.ctx || document.getElementById(this.chartId).getContext('2d');
        }
    },
    mounted() {
        this.$watch('chartData', (newVal, oldVal) => {
            this.updateGradients(newVal);

            if (!oldVal) {
                this.renderChart(
                    this.chartData,
                    this.extraOptions
                );
            }
        }, { immediate: true });
    }
}


