export default {
    horizon_bar_chartData(labelsList, lableList, dataList, color){
        const chartData = {
            labels: labelsList,
            datasets: [{
                label: lableList,
                fill: true,
                backgroundColor:[color, color, color],
                borderColor: [color, color, color],
                data: dataList,        // 차트
                barPercentage: 0.7,
            }]
        }

        return chartData;
    }
}


